/* You can add global styles to this file, and also import other style files */

@import '~normalize.css/normalize.css';

@import "sass/variables";
@import "sass/mixins";
@import "sass/buttons";
@import "sass/select";

body {
  font-family: map-get($fonts, roboto);
  background-color: map-get($layoutColors, background);
  color: map-get($layoutColors, text);
  font-weight: 300;

  @media (min-width: $medium)
  {
    // header spacing
    padding-top: 130px;
  }
}

.title,
h1, h2, h3, h4, h5, h6 {
  font-family: map-get($fonts, lato);
  font-weight: 700;
  color: map-get($layoutColors, title);
}


.container {
  padding: 0 #{$tracking * 2} $tracking;
}



